.app_background{
  background-color: grey;
  display:flex;
  align-items: center;
  justify-content: center;
  min-height: 100vh;
}

.S_Container{
  background-color: red;
  width: 500px;

  height: min-content;
  border-radius: 5px ;
  padding: 20px;
  box-shadow: 10px 10px 26px 0px rgba(0, 0, 0, 0.4);

}

.add-item-box {
  background: #fbc1bb;
  color: red;
  margin-top: 1em;
  margin-bottom: 1em;
  display: flex;
  align-items: center;
  border-radius: 10px;
  padding: 5px;
}

.add-item-box {
  background: #fbc1bb;
  color: #ec645b;
  margin-top: 1em;
  margin-bottom: 1em;
  display: flex;
  align-items: center;
  border-radius: 10px;
  padding: 5px;
}

.add-item-box svg {
  margin: 5px;
}

.add-Item-Input{
  box-sizing: border-box;
  border: none;
  width: 100%;
  height: 30px;
  color: #ec645b;
  background: #fbc1bb;
}

input::placeholder {
  color: #ec645b;
}
input:focus {
  outline: 0;
}
.item_list {
  display: flex;
  flex-direction: column;
}

.item_container{
  display:flex ;
  justify-content: space-between;
  align-items: center;
  padding: 20px 0 20px 0;
  border-bottom: 1px #fbc1bb solid;

}

.item-name {
  cursor: pointer;
}

.item-name svg {
  margin-left: 5px;
  margin-right: 5px;
}
.quantity {
  display: flex;
  align-items: center;
  border: 1px solid #ec645b;
  background: white;
  border-radius: 50px;
  font-size: 12px;
  color: #ec645b;
  min-width: 70px;
  justify-content: space-between;
}

button {
  background: transparent;
  border: none;
  margin: 3px;
  color: #ec645b;
}
button:focus {
  outline: 0;
}

button:hover {
  cursor: pointer;
}

.checked {
  text-decoration:line-through;
}

.totalcount{
  display: flex;
  justify-content: flex-end;
  padding: 10px;
  
}